/* <----------------------
header SASS 
----------------------> */

header {
   background-color: $light-blue;
   box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.5);
   display: flex;
    transition: 1s;
   padding-left:360px;
   position: relative; 
   &.active {
    transition: 1s;
    padding-left: 0px;
   }
   .navBar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    padding: 20px 0px;
    flex-wrap: wrap;
    @include media("large-tablet") {
        flex-direction: column;
     }
   }
   .navSearch {
    display: flex;
    flex-direction: row;
    position: relative;
    align-items: center;
     @include media("large-tablet") {
        justify-content: space-between;
     }
   }
  
}
.earningPoints {
    position: relative;
    margin: 20px 0;
    font-weight: 700;
    font-size: 20px;
    span {
        position: relative;
        z-index: 100;
    }
    &:after {
        content: "";
        position: absolute;
        width: 50px;
        height: 50px;
        background-color: $white;
        opacity: 0.2;
        border-radius: 50%;
        top: -13px;
        right: -12px;
        z-index: 0;
    }

}
.navControl {
    width: 40px;
    height: 40px;
    margin: 0px 30px;
    display: flex;
    align-items: center;
    cursor: pointer;
    .hamburger {
        width: 100%;
        .line {
            width: 100%;
            height: 4px;
            background-color:$primary-color;
            display: block;
            margin-top: 7px;
            margin-bottom: 7px;
            border-radius: 10px;

            &:last-child { width: 60%;}
        }
        
    }

}
.serachBar {
    width: 332px;
    position: relative;
    display: flex;
    @include media("tablet") {
        width: 100%;
     }
    .searchTerm {
        width: 100%;
        background-color: $primary-color;
        border-right: none;
        padding: 10px 20px;
        font-weight: 300;
        font-size: 16px;
        height: 56px;
        border-radius: 5px 0 0 5px;
        outline: none;
        color: $white;
        border: 0;

        &:focus {
            color: $white;
        }
        
      }
      .searchButton {
        width: 56px;
        height: 56px;
       
        background: $secondary-color;
        text-align: center;
        color: #fff;
        border-radius: 0 5px 5px 0;
        cursor: pointer;
        font-size: 20px;

      }
}
::placeholder {
    color: $white;
  }

  .userInfo {
    display: flex;
    align-items: center;
    padding-right: 20px;
    @include media("large-tablet") {
        justify-content: flex-end;
        margin-top: 10px;
     }
    .calender { 
        background-color: $primary-color; 
        padding:10px; 
        border-radius: 5px; 
        color: $white; 
        font-size: 13px; 
        display: flex; 
        align-items: center; 
        margin-right: 10px;
        border: 0.5px solid $white;
        box-shadow: 0px 0px 4px 0px rgba(178, 178, 178, 0.50);
        @include media("tablet") {
           width: 130px;
         }

        img {
            margin-right: 10px;
        }
    }
    .chat , .notification{
        background-color: $primary-color; 
        width: 40px; 
        height: 40px; 
        display: flex; 
        justify-content: center; 
        align-items: center; 
        border-radius: 50%; 
        margin-right: 10px; 
        position: relative;
        border: 0.5px solid $white;
        box-shadow: 0px 0px 4px 0px rgba(178, 178, 178, 0.50);
    }
    
    .chat {
        cursor: pointer;
        span {
            display: inline-block;
            vertical-align: middle;
            text-align: center;
            font-size: 10px;
            background: #00DD3E;
            color: #ffffff;
            border-radius: 50%;
            width: 13px;
            height: 13px;
            position: absolute;
            top: 6px;
            right: 4px;
           
        }
    }
    .notification {
        span {
          
            background-color: #FF0000;
            display: inline-block;
            vertical-align: middle;
            text-align: center;
            font-size: 10px;
            
            color: #ffffff;
            border-radius: 50%;
            width: 13px;
            height: 13px;
            position: absolute;
            top: 6px;
            right: 4px;
        }
    }
  }