.button { 
    
    padding: 16px; 
    font-size: 14px; 
    font-weight: 400; 
    box-shadow: 0px 0px 1px 1px rgba(0, 0, 0, 0.25);
    display: block;
    border-radius: 5px;
    text-decoration: none;

    &Default {
        background: #F9F9F9;
        color: black;

        &:hover {
            background-color: $secondary-color; 
            color: $white;
        }
    }
    &Primary {
        background-color: $primary-color; 
        color: $white;
        &:hover {
            background-color: $secondary-color; 

        }
    }
   
}

.buttonStatus {
    border-radius: 3px;
    padding: 10px 20px;
    color: #fff;
    text-decoration: none;
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: center;

    &.Purple {
        background: #BFA0FF !important;
    }
    &.blueMedium {
        background: #6470FF !important;
    }
    &.blueDark {
        background: #2C50EE !important;
    }
    &.gray {
        background: #A7A7A7 !important;
    }
}

.description {
    text-align: left;
    padding: 0px 20px;
}