body > *{
   // overflow-y: scroll;
	scrollbar-width: thin;
	scrollbar-color: rgba(111, 133, 147, 0.1) #fff0;
}
::-webkit-scrollbar {
	width: 5px;
	opacity:0;
}
/* ::-webkit-scrollbar-thumb{
	background:  rgba(111, 133, 147, 0.0); 
} */
::-webkit-scrollbar-thumb{
	background:  rgba(111, 133, 147, 0.1); 
}

.chat-container {
  position: absolute;
  right: 0;
  top: 100%;
  min-width: 270px;
  max-width: 270px;
  box-shadow: 0 0 2.3125rem rgba(8, 21, 66, 0.05);
  left: auto;
  background-color: #fff;
  border-radius: 10px;
  z-index: 99;
  padding-bottom: 16px;
  display: none;
}
.dlab-scroll {
    position: relative;
    overflow-y: scroll;
    padding: 12px;
}

.widget-media{
	.timeline{
		.timeline-panel{
			display: flex;
			align-items: center;
			border-bottom:0.0625rem solid #ccc;
			padding-bottom: 0.9375rem;
			margin-bottom: 0.9375rem;
			
			.media{
				width: 3.125rem;
				height: 3.125rem;
				background: #eee;
				border-radius: 0.75rem;
				overflow: hidden;
				font-size: 1.25rem;
				text-align: center;
				display: flex;
				align-items: center;
				justify-content: center;
				font-weight: 700;
				align-self: start;
			}
			.media-body{
        padding-left: 8px;
				p{
					font-size: 0.875rem;
					line-height:1.5;
				}
			}
			.dropdown {
				align-self: self-end;
				margin-top: 0.3125rem;
			}
		}
		li:last-child {
			.timeline-panel{
				margin-bottom:0;
				border-bottom:0;
				padding-bottom:0;
			}
		}
	}
}
.card[class*="bg-"]{
	.timeline{
		.timeline-panel{
			border-color:rgba(255,255,255,0.2) !important;
		}
	}
}

.dropdown-menu-end .all-notification {
    display: block;
    padding: 0.9375rem 1.875rem 0;
    color: #6470FF;
    text-align: center;
    border-top: 0.0625rem solid #c8c8c8;
    text-decoration: none;
    font-size: 12px;
}
.welcomeUser {
	cursor: pointer;
	@include media("tablet") {
       img {
		width: 40px;
	   }
     }
}
#datepicker {
	background: transparent;
    outline: none;
    border: 0px;
    color: #fff;
}