
/*---------- Dashboard------------*/
.dashBoard { 
    width: 100%;
    padding: 20px;
    padding-left: 380px;
    transition: 1s;
}

.totalSection {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 20px;
  grid-row-gap: 0px;
  margin-bottom: 20px;
  @include media("tablet") {
   display: flex;
   flex-direction: column;
   }


    .totalInfo {  
        background-color: $white; 
        padding: 20px;
        box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        @include media("tablet") {
           margin-bottom: 10px;
            }
        h2 { 
            font-size: 22px; 
            font-weight:500;
             padding: 10px 0;
             color:$gray-dark;

             @include media("tablet") {
                font-size: 18px; 
                 }
            }

            @include media("tablet") {
               .icon {
                img {
                    width: 30px;
                }
               }
            }
    }
}

.totalBarSection {
    margin-top: 10px;

    .totalNumber { 
        font-weight: 800; 
        color: $primary-color; 
        font-size: 16px;
    }
}

.progressBar {
    width: 100%;
    height: 5px;
    position: relative;
    background-color: $light-blue;
    border-radius: 20px;
    margin: 10px 0;

    &Front { 
        width: 50%;
        height: 5px;
        position: absolute;
        background-color: $primary-color;
        border-radius: 20px;
    }
}
.barPercent {
    font-size: 13px;
}

.VendorsStatistics {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 20px;
    @include media("tablet") {
        flex-direction: column;
         }
    .VendorsPaiChart {
        background: $primary-color; 
        border-radius: 5px;
        padding: 20px;
        flex:0 0 24%;
        max-width: 24%;
        @include media("tablet") {
            flex:0 0 100%;
          max-width: 100%;
             }
        .vsTitle {
            color: $white;
            font-size: 18px;
            font-weight: 600;
        }
        .vsSubTitle {
            color: $white;
            font-size: 13px;
            font-weight: 500;
        }
        .vsBottomInfo {
            display: flex;
            font-size: 13px;
            color: $white;

            .inviteSent {
                display: flex;
                align-items: center;
                padding: 10px;
                span {
                    display: block;
                    width: 10px;
                    height: 10px;
                    background: $white;
                    border-radius: 50%;
                    opacity: 0.5;
                    margin-right: 5px;
                }
            }
            .vendorsJoin {
                display: flex;
                align-items: center;
                padding: 10px;
                span {
                    display: block;
                    width: 10px;
                    height: 10px;
                    background: $white;
                    border-radius: 50%;
                    margin-right: 5px;
                }
            }
        }
    }
    .vendorRevenue {
        flex:0 0 49%;
        max-width: 49%;
        min-height: 300px;
        box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.25);
        background-color: $white;
        padding:20px;
        border-radius: 5px;
        @include media("tablet") {
            flex:0 0 100%;
          max-width: 100%;
          margin-top: 20px;
             }
        .revenueHead {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 20px;
            
            .revenueTitle {
                color: $gray-medium;
                font-size: 18px;
                font-weight: 600; 
            }
        }
       
        .incomeExp {
            display: flex;
            align-items: center;
           
            .income {
                color: $gray-medium;
                display: flex;
                align-items: center;
                margin-right: 20px;
                font-size: 14px;
                
                span {
                    display: block;
                    width:24px;
                    height: 15px;
                    background-color: $secondary-color;
                    border-radius: 24px;
                    margin-right: 5px;
                }

            }
            .expense {
                color: $gray-medium;
                display: flex;
                align-items: center;
                font-size: 14px;
               
                span {
                    display: block;
                    width:24px;
                    height: 15px;
                    background-color:$primary-color;
                    border-radius: 24px;
                    margin-right: 5px;
                }
            }
        }

        &.adviserGraph {
            flex:0 0 100%;
            max-width:100%;

            .revenueHead {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                @include media("large-tablet") {
                    flex-direction: column;
                 }
                .statusDuraion {
                    @include media("large-tablet") {
                       margin-top: 30px;
                     }
                    a {
                        border-radius: 5px;
                        border: 1px solid #6470FF;
                        background: #FFF;
                        color:#676767 ;
                        font-size: 18px;
                        text-decoration: none;
                        padding: 10px 20px;
                        margin-left: 10px;

                        &:hover {
                            background: #6470FF;
                            color: #fff;
                        }
                    }
                }
            }
        }
    }

    .vendorReview {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex:0 0 24%;
        max-width: 24%;

        @include media("tablet") {
            flex:0 0 100%;
          max-width: 100%;
          margin-top: 20px;
             }
        .vendorOrderBox, .vendorRevenueBox, .vendorEarningBox {
            width: 100%;
            background-color: $white;
            border-radius: 5px;
            box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.25);
            display: flex;
            padding: 20px;
            align-items: center;
            min-height: 140px;
            @include media("tablet") {
              min-height:auto;
              margin-bottom: 20px;
             }
            .vendorIcons {
                width: 50px;
                height: 50px;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: $light-blue;
                margin-right: 10px;
            }
            .vendorText {
                font-size:24px;
                color: $primary-color;
                font-weight: 700;
                display: flex;
                flex-direction: column;
                margin-right: 20px;
                span {
                    font-size: 12px;
                    color: $gray-dark;
                    font-weight: 500;
                }
            }
            .vendorPercent  {
                color:#00D82F;
                font-size: 10px;

                &.down {
                    color: red;
                }
            }
        }
    }
}

.vendorGraph{
    margin-top: 20px;
    min-height: 300px;
}
.VendorsStatisticsOffer {
    display: flex;
    margin-top: 20px;
    justify-content: space-between;
    @include media("tablet") {
     flex-direction: column;
         }
