         }
    }
}
.mainformSection {
    width: 100%;
    background-color: $white;
    box-shadow: 0px 0px 2px 0px #6470FF;
    padding: 20px 10px;
    margin-top: 20px;
   

    &Title {
        font-size: 20px;
        font-weight: 700;
        color: $gray-dark;
        margin-bottom: 30px;
        padding: 0px 0px 0px 16px;

        @include media("tablet") {
            margin-bottom: 10px;
         }
    }

}
.fieldContainer { 
    display: flex; 
    flex-wrap: wrap; 
    justify-content: space-between;

    @include media("large-tablet") {
      flex-direction: column;
     }
}
.flex.formFlield {
    flex-direction:column; 
} 
.fieldGroup {
    display: flex; 
    flex-direction: column; 
    padding: 10px 16px;
    width: calc(100% / 2 - 20px);
    @include media("large-tablet") {
        width: 100%;
       }
    label {
        padding: 10px 0;
        color: $gray-medium;
        font-size: 18px;
    }
    input[type="text"],input[type="password"], textarea {
        border-radius: 5px;
        background: #FFF;
        box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.25);
        padding: 16px 10px;
        border: 0;
    }
    input[type="file"] {
        border-radius: 5px;
        background: #FFF;
        box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.25);
        padding: 16px 10px;
        border: 0;
    }
    select {
        border-radius: 5px;
        background: #FFF;
        box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.25);
        padding: 15px 10px;
        border: 0;
    }

}



.uploadFileSection {
    width: 100%;
    margin-top:20px;
    .fieldGroup {
        label {
            display: flex;
            align-items: center;
        }
    }   
}
.checkBoxSection {
    display: flex;
    padding: 20px;
     
    input {
    
            padding: 0;
            height: initial;
            width: initial;
            margin-bottom: 0;
            display: none;
            cursor: pointer;
            margin-right: 20px;

            &:checked {
                & + label {
                    &:after {
                        content: '';
                        display: block;
                        position: absolute;
                        top: -6px;
                        left: 11px;
                        width: 8px;
                        height: 20px;
                        border: solid #6470FF;
                        border-width: 0 3px 3px 0;
                        transform: rotate(45deg);
                    }
                }
            }
     
    }
    label {
        margin-left: 0px;
        position: relative;
        cursor: pointer;

        &:before {
            content: '';
            -webkit-appearance: none;
            background-color: #D9D9D9;
            border: 2px solid #6470FF;
            box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
            padding: 8px;
            display: inline-block;
            position: relative;
            vertical-align: middle;
            cursor: pointer;
            margin-right: 5px;
            border-radius: 3px;
        }
    }

}
.buttonSection {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding: 10px;
    .button {
        margin:0px 10px;
    }
}

.DescriptionSection { width: 100%;}


/*----Login-----------*/

.container {
    max-width: 540px;
    margin: 0 auto;
    padding: 30px 60px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0px 0px 10px 1px #6470FF;
    margin-top: 100px;
  
    h2 {
      text-align: left;
      font-size: 35px;
      font-weight: 700;
      margin-bottom: 30px;
      color: $secondary-color;
    }
    label {
      display: block;
      width: 100%;
      margin-bottom: 10px;
      
    }
  
    input {
      &[type="text"], &[type="password"] {
        display: block;
        width: 100%;
        margin-bottom: 30px;
        border: 1px solid rgba(100, 112, 255, 0.70);
        padding:20px 10px;
        border-radius: 10px;

      }
  
      &[type="submit"] {
        display: block;
        width: 100%;
        margin-bottom: 10px;
        padding: 20px;
        background-color: $purple;
        border: none;
        color: $white;
        cursor: pointer;
        font-weight: bold;
        font-size: 20px;
        border-radius: 10px;

        &.Register {
            background-color: $purple-light;
            color: black;
        }
  
        &:hover {
          background-color: $purple-light;
        }
      }
    }
    
    ::placeholder {
        color: $gray-dark;
    }

    .forgPass { text-align: center; color: $primary-color; display: block; text-decoration: none; font-size: 20px;}
    .dontPass { text-align: center; color: $gray-dark; margin-top: 30px; margin-bottom: 20px;  font-size: 20px; }
  }

