/* <----------------------
sidebar SASS 
----------------------> */

.sideBar  {
    background-color: $primary-color;
    width: 360px;
    height: 100%;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    color: $white;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: -112px;
    font-weight: 300;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    transition: 1s;
    &.active {
        left: -360px;
        transition: 1s;
    }
    .brandLogo {
        margin:30px 0px;
    }
    .sideBarMenu {
        display: flex;
        width: 100%;
        ul {
            width: 100%;
            margin-top:20px;
            li {
                width: 100%;
                padding: 10px 20px;
                display: flex;
                align-items: center;
                cursor: pointer;
                img { width: 20px;  margin-right: 10px;}

                &:hover {
                    background-color: $secondary-color;
                    border-radius: 0px 0px 50px 0px;
                }
                &.active {
                    background-color: $secondary-color;
                    border-radius: 0px 0px 50px 0px;
                }
            }
        }
    }
}


.sideBar.active  {
   & + .dashBoard {
    padding-left: 20px;
    transition: 1s;
   }
}