
$breakpoints: (
      'very-small':(max-width:360px),
	    'small-mobile':( max-width:  414px ),
		  'mobile':( max-width:  767px ),
	  	'tablet':( max-width:  812px ),
      'medium-tablet':(max-width:850px),
      'small-laptop':(max-width:960px),
      'mini-laptop':(max-width:1025px),
	  	'large-tablet':( max-width:  1199px ),
	  	'small-desktop':  ( max-width: 1200px ),
	  	'laptop': 			( max-width:  1366px )
) !default;

/// Mixin to manage responsive breakpoints
/// @param {String} $breakpoint - Breakpoint name
/// @require $breakpoints

@mixin media($breakpoint) {
  // If the key exists in the map
  @if map-has-key($breakpoints, $breakpoint) {
    // Prints a media query based on the value
    @media #{inspect(map-get($breakpoints, $breakpoint))} {
      @content;
    }
  }
 
// If the key doesn't exist in the map
  @else {
    @warn "Unfortunately, no value could be retrieved from `#{$breakpoint}`. "
        + "Available breakpoints are: #{map-keys($breakpoints)}.";
  }
}


